import React from 'react';
import reactDom from "react-dom";
import { BrowserRouter, Route, Switch } from 'react-router-dom';

import "./assets/css/bootstrap.min.css";
import "./assets/css/font-awesome.min.css";
import "./assets/css/animate.css";
import "./assets/css/owl.carousel.css";
import "./assets/css/slick.css";
import "./assets/css/off-canvas.css";
import "./assets/fonts/linea-fonts.css";
import "./assets/fonts/flaticon.css";
import "./assets/css/magnific-popup.css";
import "./assets/css/rsmenu-main.css";
import "./assets/css/rs-spacing.css";

import "./style.css";
import "./assets/css/responsive.css";

import Home from "./view/home/home";
import Course from "./view/course/course"
import Contact from "./view/contact/contact";
import Blog from "./view/blog/blog"

import { appendScript } from './controller/scriptappend';
class App extends React.Component {

    componentDidMount() {
        appendScript("../../assets/js/modernizr-2.8.3.min.js");
        appendScript("../../assets/js/jquery.min.js");
        appendScript("../../assets/js/bootstrap.min.js");
        appendScript("../../assets/js/rsmenu-main.js");
        appendScript("../../assets/js/jquery.nav.js");
        appendScript("../../assets/js/owl.carousel.min.js");
        appendScript("../../assets/js/slick.min.js");
        appendScript("../../assets/js/isotope.pkgd.min.js");
        appendScript("../../assets/js/imagesloaded.pkgd.min.js");
        appendScript("../../assets/js/wow.min.js");
        appendScript("../../assets/js/skill.bars.jquery.js");
        appendScript("../../assets/js/jquery.counterup.min.js");
        appendScript("../../assets/js/waypoints.min.js");
        appendScript("../../assets/js/jquery.mb.YTPlayer.min.js");
        appendScript("../../assets/js/jquery.magnific-popup.min.js");
        appendScript("../../assets/js/plugins.js");
        appendScript("../../assets/js/contact.form.js");
        appendScript("../../assets/js/main.js");
    }
    render() {
        return (

            <Switch>

                <Route exact path="/" component={Home} />
                <Route exact path="/course" component={Course} />
                <Route exact path="/contact" component={Contact} />
                <Route exact path="/blog" component={Blog} />

            </Switch>
        )
    }
}

reactDom.render(<BrowserRouter  basename ="/react-test">
    <App />
</BrowserRouter>, document.getElementById("root"));
